import Vue from 'vue'
import Router from 'vue-router'
import App from '@/App'
import Home from '@/views/Index'
import Edit from '@/views/edit/Edit'
import Read from '@/views/read/Read'
import Tree from '@/views/tree/Tree'

Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        redirect: '/home'
    }, {
        path: '/home',
        name: 'Home',
        component: Home,
    }, {
        path: '/edit',
        name: 'Edit',
        component: Edit,
    }, {
        path: '/read',
        name: 'Read',
        component: Read
    }, {
        path: '/tree',
        name: 'Tree',
        component: Tree
    }]
})

/**
 * , {
        path: '/vuetool',
        name: 'VueTool',
        component: VueTool
    }, {
        path: '/app',
        name: 'App',
        component: App
    }, {
        path: '/koa',
        name: 'Koa',
        component: Koa
    }
 */


