<template>
  <div class="sudu">
    <div class="sudu-item" v-for="(item,index) in data" :key="index" @click="$emit('on-sudo',item);choose(index)">
      <div class="sudu-item-div over_text" :class="{'sudu-item-choose':active==index}" >
        {{item.tagName}}
      </div>
    </div>
  </div>

</template>
<script>

export default {
  name: 'sudoku',
  componentName: 'sudoku',
  props:{
    data: Array
  },
  data () {
    return {
      active: -1
    }
  },
  methods:{
    choose(index){
      this.active = index
    }
  },
  mounted(){
    // this.$emit('on-sudo',this.data[0]);
  }
}
</script>
<style scoped lang="scss">
.sudu{
  border-bottom: 1px solid #e0e0e0;
  &-item{
    width: 33.33%;
    height: 40px;
    display: inline-block;
    align-items: center;
    &-div{
      align-items: center;
      justify-content: center;
      display: flex;
      height: 100%;
      text-align: center;
      font-size: 14px;
      font-weight: bold;
      color: rgba(64, 158, 255,.9);
      cursor: pointer;
    }
    &-choose,&-div:hover{
      color: #fff;
      background-color: rgba(64, 158, 255,.9);
    }
  }
}
</style>


