<template>
  <div class="read">
    <nav-bar/>
    <el-row style="margin:0 6%;" >
      <el-col :span="18" class="read-left">
        <div v-html="renderHtml" class="markdown-body"></div>
      </el-col>
      <el-col :span="5" :offset="1" class="read-right">
        
       <div id="card_div" v-show="tags.length>0">
          <div class="read-tag">
            <div class="read-tag-card" v-for="(item,index) in tags" :key="index">
              <div class="read-tag-card-div over_text" :class="{'read-tag-card-choose':activeIndex==index}" @click="doNotes(item,index)" >
                {{item.tagName}}
              </div>
            </div>
          </div>

          <div class="read-notes" v-show="tagNotes.length>0">
              <div class="read-notes-div over_text" v-for="(tag,index) in tagNotes" @click="doNotesHtml(tag)" :key="index">
                <span>{{index+1}}</span>{{tag.name}}
              </div>
          </div>
        </div>
        
      </el-col>
    </el-row>
  </div>
</template>

<script>
import NavBar from '../../components/navBar.vue'
export default {
  components: {
    NavBar
  },
  data () {
    return {
      renderHtml: '',
      tags:[],
      activeIndex: 0,
      tagNotes:[]
    }
  },
  methods:{
    query(){
      const me = this
      if(this.$route.params){
        this.queryNotesHtml(this.$route.params.url)
        this.$http.post('/edit/queryTags.next').then((res)=>{
          if(res){
            this.queryNotesByTagId(res.data.tags[0].tagId)
            this.tags = res.data.tags
          }
        },(err)=>{
        })
      }
    },
    queryNotesHtml(url){
      url = url||'/notes/default'
      this.$http.post('/edit/queryNotes.next',{url:url}).then((res)=>{
        if(res){
          this.$nextTick(function () {
            this.renderHtml = res.data.render
          })
        
        }
      },(err)=>{
      })
    },
    queryNotesByTagId(tagId){
      const me = this
      this.$http.post('/edit/queryNotesByTagId.next',{tagId:tagId}).then((res)=>{
        if(res){
          this.tagNotes = res.data
        }
      },(err)=>{
      })
    },
    doNotes(key,index){
      this.activeIndex = index 
      this.queryNotesByTagId(key.tagId)
    },
    doNotesHtml(key){
      this.queryNotesHtml(key.url)
    }
  },
  mounted(){
    this.query()
  }
}
</script>


<style scoped lang="scss">
.read{
  width: 100%;
  min-height: 100%;
  background-color: #f4f5f5;
  &-left{
    margin:10px 0;
    background-color:#fff;
    padding:0 10px;
    box-shadow: 0 0 10px 1px #e0e0e0;
  }
  &-right{
    margin-top: 10px;
    #card_div{
      position:fixed;
      border-radius: 2px;
      max-width: 240px;
      background-color:#fff;
      box-shadow: 0 0 10px 1px #e0e0e0;
    }
  }
  &-tag{
    border-bottom: 1px solid #e0e0e0;
    h1,h2,h3,h4,h5,h6 {
      margin: 0;
    }
    &-card{
      width: 33.33%;
      height: 50px;
      display: inline-block;
      align-items: center;
      &-div{
        align-items: center;
        justify-content: center;
        display: flex;
        height: 100%;
        text-align: center;
        font-size: 14px;
        color: rgba(64, 158, 255,.9);
        cursor: pointer;
      }
      &-choose{
        background-color: rgba(64, 158, 255,.9);
        color: #fff;
      }
      &-div:hover{
        background-color: rgba(64, 158, 255,.9);
        color: #fff;
      }
    }
  }
  &-notes{
    margin-top: 10px;
    padding-top: 10px; 
    &-div{
      background-color: rgba(64, 158, 255,.9);
      color: #fff;
      padding:3px 10px;
      display: block;
      margin-bottom:3px;
      max-width: max-content;
      font-size: 14px;
      cursor: pointer;
      span{
        padding-right: 10px;
        border-right: 1px solid #fff;
        margin-right: 5px;
      }
    }
  }
}
</style>


