<template>
  <li>
    <div v-show="model.length==0" style="padding:10px;text-align:center;margin-top:30%">
      暂无数据
    </div>
    <div v-for="(item,index) in model" :key="index">
      <div
        :class="{bold: isFolder(item)}"
        @click="toggle(item)"
        @dblclick="changeType(item)"
        @contextmenu="showMenu(item)">
        <i v-if="isFolder(item)" class="iconfont icon-color icon-wenjianjia"></i>
        <i v-if="!isFolder(item)" class="iconfont icon-color icon-ruanjiankaifabao"></i>
        <i v-if="isFolder(item)" class="iconfont icon-color" :class="{'icon-add':!item.open,'icon-jian':item.open}"></i>
        <span @dblclick.stop="goEdit(item)">{{ item.name }}</span>
        
        <div id="menu" class="tree-menu" @click.stop="">
          <div style="color:rgba(64, 158, 255, .8);margin-bottom:5px;">
            <i class="iconfont icon-liucheng"></i>
            <span>Easy Notes</span>
          </div>
          <el-radio v-model="radio" label="A">文件夹</el-radio>
          <el-radio v-model="radio" label="B">文件</el-radio>
          <el-input style="height:35px;border:none;margin:5px 0;" v-model="input" placeholder="请输入名称"></el-input>
          <div class="tree-menu-btn" @click="addFolder">创建</div>
        </div>
      </div>
      <ul v-show="item.open" v-if="isFolder(item)">
        <tree
          class="item"
          v-for="(model, index) in item.children"
          :onChoose="onChoose"
          :key="index"
          :model="[model]"/>
      </ul>
    </div>
  </li>
</template>
<script>
  export default {
    name: 'tree',
    componentName: 'tree',
    props: {
      model: Array,
      onChoose: Function
    },
    data: function () {
      return {
        radio: 'A',
        input: '',
        chooseItem: window.chooseItem||{}
      }
    },
    computed: {
     
    },
    mounted(){
      
    },
    methods: {
      isFolder: function (item) {
        return item.children &&
          item.children.length>=0
      },
      toggle: function (item) {
        event.preventDefault();
        this.onChoose(item)
        if (this.isFolder(item)) {
          if(item.hasOwnProperty('open')){
            item.open = !item.open
          }else{
            this.$set(item,'open',true)
          }
        }
      },
      changeType: function (item) {
        console.log(item)
      },
      showMenu: function(item){
        event.preventDefault();
        var menu=document.querySelector("#menu");
        menu.style.left=event.clientX/2-20+'px';
        menu.style.top=event.clientY/2+'px';
        menu.style.display='block';
        window.chooseItem = item;
        window.onclick = function(){
          const dom = document.querySelector('#menu')
          dom&&dom.style?dom.style.display='none':'';
        }
      },
      addFolder:function(){
        //todo...
        const chooseObj = window.chooseItem
        const children = chooseObj.children;
        const res = children.filter(function(v){
          return !v.hasOwnProperty('children')
        })
        var obj = {
          name: this.input||(this.radio == 'A'?'新建笔记':'新建文件夹'),
          url: '/notes/'+chooseObj.id+'/'+chooseObj.id+'-'+(res.length+1),
          pId: chooseObj.id,
          id: chooseObj.id+"-"+(res.length+1)
        }
        this.radio == 'A'?obj.children = []:''
        chooseObj.children.push(obj)
        this.input = ""
        document.querySelector("#menu").style.display='none';
        this.$http.post('/edit/saveTree.next',{tree:this.model}).then((res)=>{
          this.$message({
            message: '创建成功',
            type: 'success'
          });
        },(err)=>{
          alert(err)
        })
      },
      renameNote(item,index){

      },
      goEdit(item){
        //命名路由跳转,传递参数使用params
        if(!this.isFolder(item)){
          this.$router.push({name: 'Edit',params:item})
        }
      }
    }
  }
</script>
<style scoped>
.item {
  cursor: pointer;
}
.icon-color{
  color: #409eff;
}
.bold {
  font-weight: bold;
}
ul {
  padding-left: 1em;
  line-height: 1.5em;
  list-style-type: dot;
  margin: 5px 0;
}
li{
  list-style: none; 
}
.tree-menu{
  display: none;
  background: #fff;
  position: absolute;
  padding:10px;
  color: #fff;
  border-radius: 5px;
  box-shadow: 1px 1px 5px 1px rgba(64, 158, 255, .8);
  width: 200px;
  position:absolute;
}
.tree-menu-btn{
  margin:5px 0;
  width:100%;
  height:40px;
  line-height:40px;
  text-align: center;
  background-color: rgba(64, 158, 255, .8);
}
.tree-item-choose{
  background-color: rgba(64, 158, 255, .8);
  color: #fff;
}
.tree-item-no{
  background-color: transparent !important;
  color: #000 !important;
}
</style>


