import axios from 'axios'
import qs from 'qs'
import config from '../config/config'

let httpOptions = {
    isLoading: true
}
// axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8'; //配置请求头
axios.defaults.baseURL = config.SERVER_ADDRESS;
axios.defaults.timeout = config.HTTP_TIME_OUT;

axios.interceptors.response.use((response) => {
    hideShade()
    return response.data;
}, (error) => {
    hideShade()
    return Promise.reject(error);
});

// //POST传参序列化(添加请求拦截器)
axios.interceptors.request.use((config) => {

    // if (config.method === 'post') {
    //     config.data = qs.stringify(config.data);
    // }
    if(httpOptions.isLoading){
        createShade()
    }
    return config;
}, (error) => {
    return Promise.reject(error);
});

function createShade(){
    const dom = document.getElementById("http-div")
    if(!dom){
        const div = document.createElement('div')
        div.id = "http-div"
        div.style.position="fixed"
        div.style.display="block"
        div.style.opacity = "0"
        div.style.transition= "all .5s ease-in"
        div.style.webkitTransition = "all .5s ease-in"
        div.style.top = 0
        div.style.left = 0
        div.style.right = 0
        div.style.bottom = 0
        div.style.background = "rgba(0,0,0,0.3)"
        div.style.zIndex = 9999
        document.body.appendChild(div)
        setTimeout(()=>{
            div.style.opacity = 1
        })
    }else{
        dom.style.opacity = 1
        dom.style.zIndex = 9999
    }
}

function hideShade(){
    setTimeout(()=>{
        const dom = document.getElementById("http-div")
        if(dom){
            dom.style.opacity = 0
            dom.style.zIndex = -1
        }
    },200)
}
export default {
    post: function(url, params, options) {
        httpOptions.isLoading = options?options.isLoading:true
        return axios.post(url,params)
    },
    get: function(url, params, options) {
        httpOptions.isLoading = options?options.isLoading:true
        return axios.get(url, { params: params })
    }
}


