<template>
  <el-row>
    <div class="nav-bar">
        <el-col :span="10" style="height:100%">
          <div class="menu-left">
            <i @click="$emit('on-open-panel')" class="iconfont icon-liucheng"></i>
            <span style="font-weight: bold;">{{noteTitle}}</span>
          </div>
        </el-col>
        <el-col :span="6" class="nav-bar-btn">
          <ul class="menu-center">
            <!-- <li @click="$emit('on-save-notes')">保存</li>
            <li @click="$emit('on-publish-notes')">发布</li> -->
          </ul>
        </el-col>
        <el-col :span="8" class="nav-bar-menu">
          <ul class="menu-right">
            <router-link to="/read">
              <li><i class="iconfont icon-bangzhuzhongxin"></i>笔记中心</li>
            </router-link>
            <router-link to="/edit">
              <li><i class="iconfont icon-console"></i>写文章</li>
            </router-link>
             <router-link to="/tree">
              <li><i class="iconfont icon-liucheng"></i>笔记树</li>
            </router-link>
          </ul>
        </el-col>
    </div>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        noteTitle:'Vue学习笔记',
        noteStatus: '编辑中..',
        noteSize: '20万字',
        noteNodes: [{
          name:'JavaScript',
          url:'/read',
        },{
          name:'第一章',
          url:'/tree'
        }]
      };
    },
    methods:{
      
    },
    mounted(){
      
      this.noteTitle = this.$route.params.name||'DefaultNotes'
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  height: 60px;
  border-bottom: 1px solid rgba(64, 158, 255,.9);
  background-color: rgba(64, 158, 255,.9);
  // position: fixed;
  // top: 0;
  // width: 100%;
  // z-index: 2000;
  &-btn{
    border-right:1px solid #e0e0e0;
    height:100%;
    display: flex;
    align-items: center;
  }
  &-menu{
    display: flex;
    height:100%;
    align-items: center;
  }
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  li{
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
  }
  .menu-left{
    display:flex;
    align-items: center;
    margin-left: 30px;
    color: #fff;
    font-size: 20px;
    height: 100%;
    i{
      top: 0;
      cursor: pointer;
    }
  }
  .menu-center{
    li{
      // background-color: rgb(64, 158, 255);
      padding: 5px 25px;
      color: #fff;
      font-size:16px;
      cursor: pointer;
    }
    li:hover{
      animation:sucaijiayuan 1s .1s ease both;
      -webkit-animation:sucaijiayuan 1s .1s ease both;
	    -moz-animation:sucaijiayuan 1s .1s ease both;
    }
  }
  .menu-right li{
    cursor: pointer;
  }
  .menu-right li:hover{
    animation:sucaijiayuan 1s .1s ease both;
    -webkit-animation:sucaijiayuan 1s .1s ease both;
	  -moz-animation:sucaijiayuan 1s .1s ease both;
  }
  @-webkit-keyframes sucaijiayuan {
    0% {
      -webkit-transform:scale(1)
    }
    10%,20% {
      -webkit-transform:scale(0.8) rotate(-2deg)
    }
    30%,50%,70%,90% {
      -webkit-transform:scale(1.1) rotate(2deg)
    }
    40%,60%,80% {
      -webkit-transform:scale(1.1) rotate(-2deg)
    }
    100% {
      -webkit-transform:scale(1) rotate(0)
    }
  }
}
</style>


