<template>
  <el-row>
    <div class="nav-bar">
        <el-col :span="16">
          <ul>
            <li style="margin-top:-5px;" @click="goHome">
              Easy Note
              <div style="font-size:12px">easy to write notes</div>
            </li>
          </ul>
        </el-col>
        <el-col :span="8">
          <ul>
            <router-link to="/read">
              <li><i class="iconfont icon-bangzhuzhongxin"></i>笔记中心</li>
            </router-link>
            <router-link to="/edit">
              <li><i class="iconfont icon-console"></i>写文章</li>
            </router-link>
             <!-- <router-link to="/tree">
              <li><i class="iconfont icon-liucheng"></i>笔记树</li>
            </router-link> -->
          </ul>
        </el-col>
    </div>
  </el-row>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '1'
      };
    },
    methods:{
      goHome(){
        this.$router.push({name: 'Home'})
      }
    }
  }
</script>
<style scoped lang="scss">
.nav-bar{
  height: 60px;
  border-bottom: 1px solid rgba(64, 158, 255,.9);
  background-color: rgba(64, 158, 255,.9);
  i{
    font-size: 20px;
    top: 3px;
    position: relative;
    margin-right: 3px;
  }
  li{
    list-style: none;
    float: left;
    margin-left: 20px;
    color: #fff;
    cursor: pointer;
  }
  li:hover{
    animation:sucaijiayuan 1s .1s ease both;
    -webkit-animation:sucaijiayuan 1s .1s ease both;
	  -moz-animation:sucaijiayuan 1s .1s ease both;
  }
  @-webkit-keyframes sucaijiayuan {
    0% {
      -webkit-transform:scale(1)
    }
    10%,20% {
      -webkit-transform:scale(0.8) rotate(-2deg)
    }
    30%,50%,70%,90% {
      -webkit-transform:scale(1.1) rotate(2deg)
    }
    40%,60%,80% {
      -webkit-transform:scale(1.1) rotate(-2deg)
    }
    100% {
      -webkit-transform:scale(1) rotate(0)
    }
  }
}
</style>


