<template>
  <div class="edit">
    <nav-bar/>
    <!-- <edit-nav-bar @on-save-notes="onSave" @on-publish-notes="onPublish"/> -->
    <save-panel :show="showPanel" @on-click-hide="hidePanel"/>
    <el-row style="height:90%;overflow: scroll;">
      <el-col :span="4" class="edit-left">
        <div class="edit-left-bar" @click="addNote">
          <i class="iconfont icon-add"></i>写文章
        </div>
        <div style="margin-top:40px">
          <div class="edit-left-list" :class="{'edit-left-choose':index==activeIndex}" v-for="(item,index) in notesList" @click="queryNotes(item.url,index)" :key="index">
            <i class="iconfont icon-color" :class="getClass(item.tagid)"></i>{{item.name}}
            <span>{{format(item.updatetime||item.createtime)}}</span>
          </div>
        </div>
      </el-col>
      <el-col :span="20" style="height:100%;overflow: scroll;">
        <mavon-editor :value="markdownValue" @save="onSave"/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import NavBar from '../../components/navBar.vue'
import SavePanel from './SavePanel.vue'
import EditNavBar from './EditNavBar.vue'
export default {
  components: {
    NavBar,
    EditNavBar,
    SavePanel
  },
  data () {
    return {
      markdownValue:'',
      activeIndex: 0,
      chooseUrl: '',
      showPanel: false,
      notesList:[]
    }
  },
  methods:{
    getClass(tagid){
      switch (tagid){
        case "001":
          return "icon-vuejs"
        case "002":
          return "icon-nodejs"
        case "003":
          return "icon-logo-javascript"
        case "004":
          return "icon-angular"
        default:
          return "icon-qita"
      }
    },
    format(time){
      return this.$util._formatDate(time)
    },
    onSave(value,render){
      if(this.chooseUrl){
        if(value||this.markdownValue){
          this.$http.post('/edit/saveNotes.next',{markdownValue:value||this.markdownValue,render:render,url:this.chooseUrl}).then((res)=>{
            this.$message({
              message: 'save success',
              type: 'success'
            });
          },(err)=>{
            alert(err)
          })
        }
      }
    },
    onPublish(){
    },
    queryNotes(url,index){
      if(url){
        this.activeIndex = index
        this.chooseUrl = url
        this.$http.post('/edit/queryNotes.next',{url:url}).then((res)=>{
          if(res){
            this.markdownValue = res.data.markdownValue
          }
        },(err)=>{
        })
      }
    },
    queryNotesAll(){
      this.$http.post('/edit/queryNotesAll.next').then(res=>{
        this.notesList = res.data
      })
    },
    addNote(){
      this.showPanel = true
    },
    hidePanel(){
      this.showPanel = false
    }
  },
  mounted(){
    this.queryNotesAll()
    this.queryNotes('/notes/default',0)
  }
}
</script>


<style scoped lang="scss">
.edit{
  height: 100%;
  width: 100%;
  &-left{
    height: 100%;
    border-right:1px solid #e0e0e0;
    overflow: scroll;
    &-bar{
      height: 40px;
      position:fixed;
      width: inherit;
      background-color: #fff;
      color:rgba(64, 158, 255,.9);
      display: flex;
      align-items: center;
      justify-content: center;
      border-bottom: 1px solid rgba(64, 158, 255,.9);
      border-right: 1px solid rgba(64, 158, 255,.9);
      cursor: pointer;
      i{
        font-size: 25px;
      }
    }
    &-list{
      padding: 5px;
      cursor: pointer;
      border-bottom: 1px solid #f1f1f1;
      color: rgba(64, 158, 255,.9);
      span{
        display: block;
        font-size: 12px;
        margin: 5px 0;
        color: #000;
      }
    }
    &-choose,&-list:hover{
      background-color: rgba(64, 158, 255,.9);
      color: #fff;
      .icon-color{
        color: #fff;
      }
      span{
        color: #fff;
      }
    }
  }
  .edit-left-col{
    height: 100%;
  }
}
.icon-color{
  color: rgba(64, 158, 255,.9);
}
</style>


