<template>
  <div class="home">
    
    <pre>
      They may take away our lives, but they'll never take our freedom!
      他们或许可以夺走我们的生命，但他们无法抢去我们的自由！

      They may take away our lives, but they'll never take our freedom!
      他们或许可以夺走我们的生命，但他们无法抢去我们的自由！
    </pre>
    <div class="home-card" v-for="(item,index) in cardItem" :key="index" >
      <div>
        {{item.name}}
      </div>
      <div class="home-card-mark" @click="goRouter(item)">
        {{item.mark}}
      </div>
    </div>
    <pre>
      
    </pre>
  </div>
</template>

<script>

export default {
  components: {
  },
  data () {
    return {
      cardItem:[{
        name:'易笔记',
        to:'/read',
        mark:'这是一个完整项目,Vue2.0+Koa2搭建;用作文档编辑和浏览,记录日常开发文档和笔记'
      },{
        name:'Koa2服务端',
        to:'/koa',
        mark:'学习Koa框架的使用'
      },{
        name:'Vue组件',
        to:'/vuetool',
        mark:'学习Vue框架,封装部分Vue组件'
      },{
        name:'混合开发',
        to:'/app',
        mark:'混合开发App技术,也是工作三年以来一直从事的工作内容'
      }]
    }
  },
  methods:{
    goRouter(item){
      this.$router.push({path: item.to})
    }
  }
}
</script>


<style scoped lang="scss">
body{
  height: inherit;
}
.home{
  height: 100%;
  margin: 0 auto;
  width: 800px;
  justify-content: center;
  position: relative;
  pre{
    font-size: 14px;
    font-weight: bold;
    font-family:"Microsoft Yahei";
    justify-content: center;
  }
  pre:first-child {
    border-bottom: 1px solid rgba(64, 158, 255,.9);
    margin-bottom: 20px;
  }
  pre:last-child{
     margin-top: 20px;
    border-top: 1px solid rgba(64, 158, 255,.9);
  }
  &-card{
    display:inline-block;
    width: 245.7px;
    margin: 9px;
    height: 150px;
    text-align: center;
    font-weight: bold;
    line-height: 150px;
    color: rgba(64, 158, 255,1);
    border: 1px solid rgba(64, 158, 255,.5);
    cursor: pointer;
    position: relative;
    div:first-child{
      background: url('../../static/001.jpg') no-repeat;
      background-size: cover;
    }
    &-mark{
      line-height: normal;
      position: absolute;
      color: #fff;
      position: absolute;
      padding: 10px;
      box-sizing: border-box;
      font-size: 12px;
      bottom: 0;
      transition:all .5s ease-in;
      opacity: 0;
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &-mark:hover{
      opacity: 1;
      color: #000;
      background: url('../../static/00.jpg') no-repeat;
      background-size: cover;
    }
  }
  &-card:hover{
    color: red;
    box-shadow: 0 0 1px 1px rgba(64, 158, 255,.9);
  }
}
</style>


