<template>
  <div v-show="show" class="panel">
    <div class="save-panel" :class="{'save-panel-full':isFullScreen}">
      <div class ="save-panel-header" @dblclick="fullScreen">
        笔记目录
        <i class="iconfont panel-full" :class="{'icon-suoxiao':isFullScreen,'icon-quanping':!isFullScreen}" @click="fullScreen"></i>
        <i class="iconfont icon-guanbi panel-close" @click="$emit('on-click-hide')"></i>
      </div>
      <div class="save-panel-content" :class="{'save-panel-content-full':isFullScreen}">
        <el-row style="height:100%">
          <el-col :span="14" class="save-panel-content-left">
            <sudoku :data="suduData" @on-sudo="onSudo"/>
            <div class="save-panel-content-left-tree">
              <tree :onChoose="onChoose" :model="treeData"/>
              <div class="save-panel-content-left-fot">
                共:{{rightObj.tagTotal}}篇
              </div>
            </div>
          </el-col>
          <el-col :span="10" class="save-panel-content-right">
            <div v-show="rightObj.tagName">
              <i class="iconfont icon-color" :class="rightObj.iconClass"></i>
              <span>{{rightObj.tagName}}</span>
            </div>
            <div v-show="treeItem.name">
              <i class="iconfont icon-color" :class="treeItem.hasOwnProperty('children')?'icon-wenjianjia':'icon-ruanjiankaifabao'"></i>
              <span>{{treeItem.name}}</span>
            </div>
            <div style="word-break: break-all;font-size: 12px;padding:0 5px">
              {{treeItem.url}}
            </div>
            
            <el-input style="height:30px;border:none;margin:10px 0;" v-model="rightObj.input" placeholder="请输入名称"></el-input>
            <el-radio v-model="rightObj.radio" label="A">文件夹</el-radio>
            <el-radio v-model="rightObj.radio" label="B">文件</el-radio>
            <div class="save-panel-btn" @click="createTree">创建</div>
            <div v-show="treeData.length>0" class="save-panel-btn" @click="createTree">删除</div>
          </el-col>
        </el-row>
      </div>
    </div>
    <div class="save-panel-bg" @click="$emit('on-click-hide')"></div>
  </div>
</template>
<script>
export default {
  components: {
  },
  props:{
    show:Boolean
  },
  data () {
    return {
      isFullScreen: false,
      treeData:[],
      rightObj:{
        radio: 'A',
        input: '',
        tagId: '',
        tagName: '',
        tagTotal: '',
        iconClass: ''
      },
      suduData:[],
      treeItem:{}
    }
  },
  methods:{
    init(){
      this.rightObj = {
        radio: 'A',
        input: '',
        tagId: '',
        tagName: '',
        iconClass: ''
      }
      this.treeItem = {}
    },
    fullScreen(){
      this.isFullScreen = !this.isFullScreen
    },
    onChoose(data){
      this.treeItem = data
    },
    createTree(){
      if(!this.rightObj.input){
         this.$message({
          message: '请输入必填项',
          type: 'info'
        });
        return
      }
      if(!this.rightObj.tagId){
         this.$message({
          message: '请选择标签',
          type: 'info'
        });
        return
      }
      let treeObj = {
        tagId:this.rightObj.tagId,
        name: this.rightObj.input,
        url: this.treeItem.url||'',
        isFolder: this.rightObj.radio == 'A'
      }
      this.$http.post('/edit/createTree.next',treeObj).then((res)=>{
        this.queryTree(treeObj.tagId)
        this.$message({
          message: '创建成功',
          type: 'success'
        });
      },(err)=>{
        alert(err)
      })
    },
    onSudo(item){
      this.init()
      this.rightObj.tagName = item.tagName
      this.rightObj.tagId = item.tagId
      this.rightObj.iconClass = this.getClass(item.tagId)
      this.queryTree(item.tagId)
    },
    queryTree(tagId){
      this.$http.post('/edit/queryTree.next',{tagId:tagId},{isLoading: false}).then((res)=>{
        if(res.success){
          this.treeData = res.data.tree
          this.rightObj.tagTotal = res.data.total
        }
      },(err)=>{
        alert(err)
      })
    },
    queryTags(){
      this.$http.post('/edit/queryTags.next').then((res)=>{
        if(res){
          this.suduData = res.data.tags
        }
      },(err)=>{
      })
    },
    getClass(tagid){
      switch (tagid){
        case "001":
          return "icon-vuejs"
        case "002":
          return "icon-nodejs"
        case "003":
          return "icon-logo-javascript"
        case "004":
          return "icon-angular"
        default:
          return "icon-qita"
      }
    }
  },
  mounted(){
    this.queryTags()
  }
}
</script>

<style scoped lang="scss">
.panel{
  .save-panel-full{
    height: 100% !important;
    width: 100% !important;
    top: 0 !important;
    left: 0 !important;
    margin: 0 !important;
  }
  .save-panel{
    left: 50%;
    height: 400px;
    width: 600px;
    position: fixed;
    top: 50%;
    z-index: 2000;
    margin: -200px -300px;
    background-color: #fff;
    border-radius: 3px;
    &-btn{
      margin:10px 0;
      width:100%;
      height:35px;
      line-height:35px;
      text-align: center;
      color:#fff;
      background-color: rgba(64, 158, 255, .8);
      cursor: pointer;
    }
    .save-panel-header{
      height: 44px;
      border-bottom: 1px solid #e0e0e0;
      line-height: 44px;
      padding-left:10px;
      color: #409eff;
      font-weight: bold;
      cursor: pointer;
      .panel-close{
        float: right;
        margin-right: 10px;
        font-size: 22px;
        color: #409eff;
        cursor: pointer;
        border-left: 1px solid #e0e0e0;
        padding-left: 10px;
      }
      .panel-full{
        float: right;
        margin-right: 10px;
        font-size: 20px;
        color: #409eff;
        cursor: pointer;
        animation:animenter 1s .1s ease both;
        -webkit-animation:animenter 1s .1s ease both;
	      -moz-animation:animenter 1s .1s ease both;
      }
      .panel-full:hover{
        animation:animleave 1s .1s ease both;
        -webkit-animation:animleave 1s .1s ease both;
	      -moz-animation:animleave 1s .1s ease both;
      }
      @-webkit-keyframes animenter {
        0% {
          -webkit-transform:rotate(0deg)
        }
        100% {
          -webkit-transform:rotate(180deg)
        }
      }
      @-webkit-keyframes animleave {
        0% {
          -webkit-transform:rotate(0deg)
        }
        100% {
          -webkit-transform:rotate(-180deg)
        }
      }
    }
    .save-panel-content{
      min-height: 356px;
      height:calc(100% - 44px);
      height:-webkit-calc(100% - 44px);
      height:-moz-calc(100% - 44px);
      box-sizing: border-box;
      overflow-y: scroll;
      &-full{
        min-height: 100% !important;
      }
      &-left{
        height:100%;
        box-sizing: border-box;
        position: relative;
        &-fot{
          font-size: 14px;
          position: absolute;
          bottom: 0;
          background: #fff;
          width: 100%;
          left: 0;
          padding: 0 5px;
        }
        &-tree{
          padding:10px;
          box-sizing: border-box;
          height: calc(100% - 81px);
          overflow: scroll;
          background-color: #f1f1f1;
        }
      }
      &-right{
        height:100%;
        border-left: 1px solid #e0e0e0;
        padding: 10px;
        span>:first-child{
          font-size: 14px;
        }
      }
    }
    .save-panel-fot{
      height: 44px;
      border-bottom: 1px solid #e0e0e0;
      line-height: 44px;
      padding-left:10px;
    }
  }
  .save-panel:hover{
    box-shadow: 0 0px 10px 3px rgba(64, 158, 255,.9);
  }
  .save-panel-bg{
    background-color: rgba(0,0,0,.3);
    position: fixed;
    top: 0;
    width: 100%;
    height:100%;
    z-index:1999;
  }
}
.icon-color{
  color: rgba(64, 158, 255,.9);
}
</style>


