<template>
  <div class="tree">
    <nav-bar/>
    <el-row>
      <el-col :span="12">
        <div class="new-note" v-show="treeData.length == 0">
          <i class="iconfont icon-add icon-new-note" @click="">新建笔记</i>
        </div>
        <div style="padding:20px">
          <tree class="item" :model="treeData"/>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import NavBar from '../../components/navBar.vue'
export default {
  components: {
    NavBar
  },
  data () {
    return {
      treeData:[]
    }
  },
  methods:{
    newNotes(){
      
    }
  },
  mounted(){
    const me = this
    me.$http.post('/edit/queryTree.next').then((res)=>{
      res.success?me.treeData = res.data:me.treeData = []
    },(err)=>{
      alert(err)
    })
  }
}
</script>


<style scoped lang="scss">
.tree{
  height: 100%;
  width: 100%;
  .new-note{
    padding:20px;
    color:#409eff;
    .icon-new-note{
      cursor: pointer;
    }
  }
}
</style>


